body{
  font-family: "Albert";
  background: #838383;
  margin:50px;
}

#quote-box{
  width: 100%;
  max-width: 400px;
  min-height: 180px;
  padding: 30px;
  background: #5B8FAB;
  margin: auto;
  box-shadow: 4px 4px 4px 3px rgba(0,0,0,.5);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 170px;
}

p{
  color: white;
  font-size: 17px;
  word-wrap: break-word;
}

p:last-of-type{margin: 10px 0;}

button{
  border:none;
  outline:none;
  margin-right: 12px;
  background: #AB775B;
  color: white;
  width: 80px;
  padding: 7px 0;
  font-size: 13px;
  border-radius: 4px;
} 

a{
  text-decoration: none;
  display: inline-block;
  text-align: center;
  color: white;
}

h2{
  color: #292525;
  padding: 0px;
  margin:0px;
}